

.signin__btn {
    border: 1px solid rgb(6, 95, 212);
    background-color: transparent;
    border-radius: 3px;
    padding: 6px 12px;
    display: flex;
    align-items: center;
    color: rgb(6, 95, 212);
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;

    svg {
        width: 24px !important;
        height: 24px !important;
        fill:rgb(6, 95, 212);
        margin-right: 10px;
    }
}