
.subscribe-btn {
    text-transform: uppercase;
                        padding: 10px 20px;
                        font-size: 14px;
                        font-family: "Roboto", "Noto", sans-serif;
                        border: none;
                        font-weight: 600;
                        border-radius: 3px;
                        cursor: pointer;

                        &.subscribe {
                            color: #fff;
                            background-color: red;
                        }

                        &.unsubscribe {
                            color: rgb(170, 170, 170);
                            background-color: rgba(255,255,255,0.1);
                        }
                    
}